.about{
  width: 100%;
  background: rgba(33, 31, 32, 1);
  width: 100%;
  padding-top: 30px;
  
  .wrapper{
    background: url('../../../public/about-bg.png');
    background-repeat: no-repeat;
    background-position: right;
    height: 610px;
    max-width: 1400px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 5;

    -webkit-box-shadow: 0px 0px 7px 7px rgba(0, 0, 0, 0.55);
    -moz-box-shadow: 0px 0px 7px 7px rgba(0, 0, 0, 0.55);
    box-shadow: 0px 0px 7px 7px rgba(0, 0, 0, 0.55);
  }

  &__container{
    display: flex;
    justify-content: space-between;
    max-width: 1400px;
    margin: 0 auto;

    background: rgb(27,27,27);
    background: linear-gradient(90deg, rgba(27,27,27,1) 20%, rgba(28,28,28,0.73) 40%, rgba(25,25,25,0) 60%);

    .about-text{
      color: #CFCFCF;
      z-index: 1;
      width: 50%;
      font-weight: 400;
      font-size: 20px;
      line-height: 25px;
      font-family: 'Montserrat Alternates', sans-serif;
      background: rgb(27,27,27);
      background: linear-gradient(90deg, rgba(27,27,27,1) 60%, rgba(28,28,28,0.73) 80%, rgba(25,25,25,0) 100%);
      padding: 0 50px;

      h2{
        font-size: 32px;
        margin: 100px 0px 25px 0px
      }
    }

    .about-cards{
      display: grid;
      grid-template-columns: 228px 228px;
      grid-template-rows: 228px 228px;
      grid-gap: 30px;
      padding: 70px 50px;
      font-family: 'Montserrat Alternates', sans-serif;
      color: #CFCFCF;

      &__card{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: rgba(80, 75, 74, 0.7);
        backdrop-filter: blur(10px);
        p{
          margin-top: 20px;
        }
      }

      &__card:hover{
        background: rgba(80, 75, 74, 0.9);
        transition: all 0.3s ease-in-out;
        cursor: pointer;
        color: rgba(121, 178, 130, 1)
      }
    }
  }

  &__btn{
    width: 223px;
    height: 59px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-sizing: border-box;
    backdrop-filter: blur(5px);
    border-radius: 10px;
    font-family: 'Montserrat Alternates', sans-serif;
    color: #fff;
    margin-top: 60px;
    font-size: 17px;
    cursor: pointer;
    &:hover{
      background: rgba(255, 255, 255, 0.1);
      transition: all 0.3s ease-in-out;
      color: rgba(121, 178, 130, 1)
    }
  }
}

@media (max-width: 1200px){

  .about{
    .about-cards{
      display: none ;
    }

    .about-text{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-evenly;
      flex: 1;
      text-align: justify;
    }

    h2{
      margin: 5px 0px 5px 0px !important;
    }
    
    .about__btn{
      margin-top: 10px;
    }
  }
}

@media (max-width: 500px){

  .about{
    .about-cards{
      display: none ;
    }

    .about-text{
      font-size: 16px;
      padding: 0 20px; 
    }
  }
  
}