.footer{
  width: 100%;
  height: 195px;
  background: rgba(33, 31, 32, 1);
  font-family: 'Montserrat Alternates', sans-serif;
  color: #fff;

  .wrapper{
    max-width: 1440px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 150px 300px 1fr; 
    height: 100%;
    background: rgba(33, 31, 32, 1);
  }

  .navigation{
    width: 100%;
    display: flex;
    justify-content: center;
    &__list{
      display: flex;
      justify-content: space-between;
      align-items: center;
      text-decoration: none;
      list-style: none;
      width: 80%;
    }

    &__link{
      color: #fff;
      &:hover{
        font-weight: bold;
        }
    }
  }

  &__link{
    color: #fff;
    margin-top: 6px;
    &:hover{
    font-weight: bold;
    }
  }

  &__container{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .btn-container{
    display: flex;
    justify-content: center;
    align-items: center;

    &__item{
      width: 64px;
      height: 64px;
      left: 38px;
      border-radius: 50%;
      cursor: pointer;
      border: none;
    }

    &__icon{
      width: 30px;
      height: 30px;
    }
  }

  @media (max-width: 1200px) {
    .navigation{
      transform: translateY(-100%);
      z-index: -20;
      ul{
        display: none;
      }
    }

    .navigation.navigation-burger{
      position: fixed;
      z-index: 1000;
      padding: 0;
      margin: 0;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(33, 31, 32, 1);;
  
      font-family: 'Montserrat Alternates', sans-serif;
      font-size: 18px;
      color: #CFCFCF;
      text-align: center;
      transition: all .3s ease-in-out;
      transform: translateY(0%);

      .navigation__link{
        color: #CFCFCF;
        text-decoration: none;
        &:hover{
          font-weight: bold;
        }
      }
  
      ul{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 80%;
        font-size: 20px;
        text-decoration: none;
        li{
          padding: 20px;
        }
      }
    }
  }
}

@media (max-width: 500px) {
  .footer{
    .wrapper{
      padding: 15px;
      grid-template-columns: 60px 1fr;
      gap: 15px
    }
  }
}
  
